import Picker from "./index";
import weapons from "../../data/weapons";
import artifacts from "../../data/artifacts";

export function WpPicker({ wpType, needMassAdd, pick, close }) {
  const data = weapons[wpType].map(({ code, name, beta, icon, rarity }) => ({
    code,
    name,
    beta,
    icon,
    rarity
  }));
  return (
    <Picker
      needMassAdd={needMassAdd}
      data={data}
      dataType={wpType}
      close={close}
      pick={({ code }) => pick(initWeapon({ type: wpType, code }))}
    />
  );
}

// [here, ./Calculator/CalcChar/helpers, ./Calculator/SetupManager/Settings/helpers,
// ./MyCharacters/index, ./MyWeapons/RightSection]
export function initWeapon({ type, code }) {
  if (!code) {
    code = { Bow: 11, Catalyst: 36, Claymore: 59, Polearm: 84, Sword: 108 }[
      type
    ];
  }
  return {
    type,
    code,
    level: "1/20",
    refinement: 1
  };
}

export function ArtPicker({ artType, needMassAdd, close, pick }) {
  const gold = [];
  const purple = [];
  for (const name in artifacts) {
    const { code } = artifacts[name];
    for (const rarity of artifacts[name].variants) {
      const { beta } = artifacts[name];
      const { icon } = artifacts[name][artType];
      if (rarity === 5) {
        gold.push({ code, beta, name, icon, rarity });
      } else {
        purple.push({ code, beta, name, icon, rarity });
      }
    }
  }
  return (
    <Picker
      needMassAdd={needMassAdd}
      data={gold.concat(purple)}
      dataType={artType}
      close={close}
      pick={({ name, rarity }) =>
        pick(initArtPiece({ type: artType, set: name, rarity }))
      }
    />
  );
}

function initArtPiece({ type, set, rarity }) {
  return {
    type,
    set,
    rarity,
    level: 0,
    mainSType: type === "flower" ? "HP" : type === "plume" ? "ATK" : "ATK%",
    subS: [
      {
        type: "DEF",
        val: 0
      },
      {
        type: "DEF%",
        val: 0
      },
      {
        type: "CRIT Rate",
        val: 0
      },
      {
        type: "CRIT DMG",
        val: 0
      }
    ]
  };
}
